<template>
  <div class="history_practise" ref="mainCard">
    <el-card class="practise_main" shadow="never">
      <div class="head">
        <div>{{dataInfo['Title']}}</div>
        <div>总分：{{score}}</div>
        <div>习题数：{{total||'0'}}</div>
        <div>
          时间: {{dataInfo['CreateTime'] }}
        </div>
      </div>

      <!--  时间轴形式-->
      <el-timeline v-if="item">
        <el-timeline-item placement="top" color="#0bbd87"
                          :timestamp="(currentIndex + 1) + '.【' + getPracticeTypeName(item.practiceInfo['PSType']) + '】'+(item.practiceInfo['PSScore']||0) + '分'">
          <el-card>
            <div class="title_info">【题目】{{item.practiceInfo['PSContent']}}</div>
            <div v-if="[1, 2].indexOf( item.practiceInfo['PSType'])>-1">
              <div class="options" v-for="(sub, k) in item.practiceOptions" :key="k">
                <span>【{{ sub['SOSerial']}}】</span>
                <span>{{sub['SOContent']}}</span>
              </div>
            </div>

            <do-answer
              :question-type="item.practiceInfo['PSType']"
              :question-option="item.practiceInfo['PSOption']"
              :answer-value="item.practiceAnswer"
              :module-picture="modulePicture"
              @change="answerChange($event, item)">
            </do-answer>

            <div class="survey_info" v-show="false">
              <span>问卷调研：</span>
              <el-button size="small" icon="el-icon-check" type="primary" disabled v-if="item.surveyMaster===1">已掌握
              </el-button>
              <el-button size="small" icon="el-icon-close" type="danger" disabled v-else-if="item.surveyMaster===0">
                未掌握
              </el-button>
              <el-button size="small" type="warning" disabled v-else>未参与调研</el-button>
            </div>
          </el-card>
        </el-timeline-item>
      </el-timeline>
      <div v-else>暂无数据</div>

      <div style="text-align: center;padding: 15px 0;display: flex;justify-content: space-around;margin-bottom: 20px;">

        <el-button size="small" style="padding: 12px 20px;" @click="$emit('toClose')">关闭</el-button>
        <el-button size="small" style="padding: 12px 20px;" type="primary" v-if="currentIndex > 0" @click="toNext(-1)">上一题</el-button>
        <el-button size="small" style="padding: 12px 20px;" type="primary" v-if="currentIndex + 1 < total" v-loading="loading" @click="toNext(1)">下一题</el-button>
        <el-button size="small" style="padding: 12px 20px;" type="primary" v-if="currentIndex + 1 === total" v-loading="loading" :disabled="disabled"
                   @click="toNext(0)">提交试卷
        </el-button>
      </div>
    </el-card>
  </div>
</template>

<script>
import api from "@/scripts/api";
import store from "@/scripts/store";
import { practiceTypeName, ModulePicture } from "@/scripts/dictionary";

export default {
  name: "",
  components: {
    DoAnswer: () => import("./DoAnswer")
  },
  props: {
    dataInfo: {
      type: Object,
      default: () => {
        return {};
      }
    }
  },
  data () {
    return {
      fromID: "",
      libraryId: "",
      taskId: "",

      total: 0,
      score: 0,
      currentIndex: 0,
      // activeIndex:0,
      modulePicture: ModulePicture.PractiseAnswer,
      dataList: [],
      loading: false,
      disabled: false,

      studentInfo: null
    };
  },
  computed: {
    item () {
      return this.dataList[this.currentIndex];
    }
  },
  created () {
    this.studentInfo = store.GetStudentInfo();
  },
  methods: {
    init () {
      this.total = 0;
      this.score = 0;
      this.currentIndex = 0;
      this.disabled = false;
      this.dataList = [];
      this.taskId = this.dataInfo["CIPID"] + "";
      this.fromID = this.dataInfo["CPID"] + "";
      this.libraryId = this.dataInfo["PLID"] + "";
      this.getPracticeInfo();
    },
    getPracticeInfo () {
      api.IClassPractise.StudentTaskStatistics(
        this.dataInfo["PLID"] + ""
      ).then(res => {
        if (res.Result) {
          this.total = parseInt(res.Total + '' || '0');
          this.score = res.Score;
          this.getDataInfo(1);
        } else {
        }
      }).catch(() => {
      });
    },
    getDataInfo (val) {
      if (val > this.total) return;

      api.IClassPractise.StudentTaskInfo(
        this.taskId,
        this.libraryId,
        val,
        this.studentInfo ? this.studentInfo.StudentID : ""
      ).then(res => {
        if (res.Result) {
          const practiceInfo = res.PSData;
          const practiceReply = res.PAData ? res.PAData : {};
          const practiceAnswer = practiceReply["PAContent"] || "";
          let surveyMaster = null;
          if (practiceReply) {
            surveyMaster = practiceReply["PAMaster"] || null;
          }

          const practiceOptions = [];
          if (res.SOData && res.SOData.length > 0) {
            res.SOData.forEach(item => {
              if (practiceOptions.indexOf(item) === -1) {
                practiceOptions.push(item);
              }
            });
          }
          this.dealDataList(val, {
            practiceInfo,
            practiceReply,
            practiceAnswer,
            surveyMaster,
            practiceOptions
          });
        } else {
          this.dealDataList(val, {});
        }
        // this.getDataInfo(val);
      }).catch(() => {
        this.dealDataList(val, {});
        // this.getDataInfo(val);
      });
    },
    dealDataList (val, item) {
      if (this.dataList.length > val) {
        this.dataList.splice(val - 1, 1, item);
      } else {
        this.dataList.push(item);
      }
      // let dialog = document.getElementsByClassName("el-dialog is-fullscreen")[0];
      // dialog.scrollTop = dialog.scrollHeight;
    },
    toNext (val) {
      if (!this.item.practiceAnswer) {
        this.$message.error("尚未作答，请先作答");
        return;
      }
      api.IClassPractise.StudentTaskSave(
        this.taskId,
        this.item.practiceReply["PAID"] || "",
        this.item.practiceInfo["PSID"] + "",
        this.studentInfo ? this.studentInfo.StudentID : "",
        this.item.practiceAnswer
      ).then(res => {
        if (res.Result) {
          this.currentIndex += val;
          if (val === 0) {
            this.submitPractice();
          } else {
            this.getDataInfo(this.currentIndex + 1);
          }
        } else {
          this.$message.error("提交答案失败");
        }
      }).catch(() => {

      });
    },
    submitPractice () {
      this.loading = true;
      api.IClassPractise.StudentTaskFinish(
        this.taskId,
        this.studentInfo ? this.studentInfo.StudentID : ""
      ).then(res => {
        if (res.Result) {
          this.$message({
            message: "提交试卷成功",
            type: "success"
          });
          this.$emit("refresh");
          this.loading = false;
          this.disabled = true;
        } else if (res.Info) {
          this.loading = false;
          this.$message.error(res.Info);
        } else {
          this.loading = false;
          this.$message.error("提交试卷失败，请重试");
        }
      }).catch(() => {
        this.loading = false;
        this.$message.error("提交试卷失败，请重试");
      });
    },
    getPracticeTypeName (val) {
      return practiceTypeName(val);
    },
    answerChange (answer, item) {
      item.practiceAnswer = answer;
    }
  }
};
</script>

<style lang='less' scoped>
@default-top: 15px;
@font-title: 20px;
@font-sub-title: 16px;
@font-info: 15px;
.history_practise {
  .practise_main {
    text-align: left;
    font-size: @font-info;
    word-break: break-all;

    .head {
      display: flex;
      justify-content: space-between;
      font-size: @font-sub-title;
      font-weight: bolder;
      padding: 0 @default-top 20px @default-top;
      margin-bottom: 20px;
      border-bottom: 1px solid #eee;
    }

    .title_info {
      //text-indent: 2*@default-top;
      font-size: @font-sub-title;
      line-height: 2;
    }

    .options {
      padding-left: 2*@default-top;
      line-height: 2;
      color: #888;
    }

    .survey_info {
      display: flex;
      justify-content: flex-start;

      line-height: 35px;
      font-size: @font-info;
      font-weight: bolder;
      color: #666;
    }
  }
}

</style>
