<template>
  <div class="comp-full-calendar">
    <!-- header pick month -->
    <fc-header
      :current-date="currentDate"
      :title-format="titleFormat"
      :first-day="firstDay"
      :month-names="monthNames"
      :tableType="tableType"
      @change="changeDateRange"
    >
      <div slot="header-left">
        <slot name="fc-header-left"></slot>
      </div>

      <div slot="header-right">
        <el-radio-group v-model="tableType" size="mini" @change="changeType">
          <el-radio-button label="day">日</el-radio-button>
          <el-radio-button label="week">周</el-radio-button>
          <el-radio-button label="month">月</el-radio-button>
        </el-radio-group>
      </div>
    </fc-header>
    <!-- body display date day and events -->
    <fc-body
      :current-date="currentDate"
      :events="events"
      :timeDivide="timeDivide"
      :month-names="monthNames"
      ref="fcbody"
      :tableType="tableType"
      :loading="loading"
      :week-names="weekNames"
      :day-name="dayName"
      :first-day="firstDay"
      :weekDays="weekDays"
      @eventclick="emitEventClick"
      @dayclick="emitDayClick"
      @moreclick="emitMoreClick"
      :dialogVisible.sync="visible"
    >
      <template slot="body-card">
        <slot name="fc-body-card"></slot>
      </template>
      <!-- <template slot="body-dlg">
        <slot name="fc-body-dlg"></slot>
      </template>-->
    </fc-body>
  </div>
</template>
<script type="text/babel">
import fcBody from "./body";
import fcHeader from "./header";
const langSets = {
  weekNames: ["周一", "周二", "周三", "周四", "周五", "周六", "周日"],
  monthNames: [
    "1月",
    "2月",
    "3月",
    "4月",
    "5月",
    "6月",
    "7月",
    "8月",
    "9月",
    "10月",
    "11月",
    "12月"
  ],
  titleFormat: "yyyy年MM月"
};
export default {
  components: {
    "fc-body": fcBody,
    "fc-header": fcHeader
  },
  props: {
    events: {
      type: Array,
      default: () => []
    },
    timeDivide: {
      type: Array,
      default: () => {
        return [
          {
            start: "08:00",
            end: "08:45"
          },
          {
            start: "08:55",
            end: "09:40"
          },
          {
            start: "10:00",
            end: "10:45"
          },
          {
            start: "10:55",
            end: "11:40"
          },
          {
            start: "14:00",
            end: "14:45"
          },
          {
            start: "14:55",
            end: "15:40"
          },
          {
            start: "16:00",
            end: "16:45"
          },
          {
            start: "16:55",
            end: "17:40"
          },
          {
            start: "19:00",
            end: "19:45"
          },
          {
            start: "19:55",
            end: "20:40"
          },
          {
            start: "21:00",
            end: "21:45"
          }
        ];
      }
    },
    firstDay: {
      type: Number || String,
      validator(val) {
        const res = parseInt(val);
        return res >= 0 && res <= 6;
      },
      default: 0
    },
    titleFormat: {
      type: String,
      default() {
        return langSets.titleFormat;
      }
    },
    monthNames: {
      type: Array,
      default() {
        return langSets.monthNames;
      }
    },
    weekNames: {
      type: Array,
      default() {
        const arr = langSets.weekNames;
        return arr.slice(this.firstDay).concat(arr.slice(0, this.firstDay));
      }
    },
    loading: {
      type: Boolean,
      default: false
    },
    dialogVisible: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      tableType: "month",
      weekDays: [],
      currentDate: new Date()
    };
  },
  created() {},
  computed: {
    dayName() {
      const arr = langSets.weekNames;
      const theDayWeek = new Date().getDay();
      return arr[theDayWeek - 1];
    },
    visible: {
      get() {
        return this.dialogVisible;
      },
      set(val) {
        this.$emit("update:dialogVisible", val);
      }
    }
  },
  methods: {
    changeDateRange(start, end, currentStart, current, weekDays) {
      this.currentDate = current;
      this.weekDays = weekDays;
      this.$emit("change", start, end, currentStart, weekDays);
    },
    emitEventClick(event, jsEvent) {
      this.$emit("eventClick", event, jsEvent);
    },
    emitDayClick(day, jsEvent) {
      this.$emit("dayClick", day, jsEvent);
    },
    emitMoreClick(day, events, jsEvent) {
      this.$emit("moreClick", day, event, jsEvent);
    },
    changeType(type) {
      this.tableType = type;
      this.$emit("changeType", type);
    }
  }
};
</script>
<style lang="less">
.comp-full-calendar {
}
</style>