  <div class="full-calendar-body">
    <div class="right-body">
      <div class="weeks">
        <div class="blank" v-if="tableType==='week'" style="width: 60px"></div>
        <template v-if="tableType==='week'||'month' && tableType!=='day'">
          <strong class="week" v-for="(week,index) in weekNames" :key="index">
            {{week}}
            <span v-if="tableType=='week' && weekDate.length">({{weekDate[index].showDate}})</span>
          </strong>
        </template>
        <template v-if="tableType=== 'day'">
          <strong class="week">{{dayName}}</strong>
        </template>
      </div>
      <div class="dates" ref="dates" v-loading="loading" v-if="tableType=='month'">
        <div class="dates-events">
          <template v-for="(week,index) in currentDates">
            <div
              class="events-week"
              v-if="week[0].isCurMonth || week[week.length-1].isCurMonth"
              :key="index"
            >
              <div
                class="events-day"
                v-for="(day,i ) in week"
                :key="i"
                track-by="$index"
                :class="{'today' : day.isToday,
                'not-cur-month' : !day.isCurMonth,
                'selected': showMoreCard === day.date}"
                @click="dayClick(day,$event)"
              >
                <p class="day-number">{{day.monthDay}}</p>
                <div class="event-box" v-if="day.events.length">
                  <div
                    class="event-item"
                    v-for="(event, j) in day.events"
                    :key="j"
                    @click="eventClick(event, $event)"
                  >
                    <span class="icon" v-if="j<maxCount"></span>
                    <p
                      class="info"
                      v-if="j<maxCount"
                      v-html="isBegin(event, day.date, day.weekDay)"
                    ></p>
                  </div>
                  <div class="show-more" v-if="day.events.length>maxCount-1">
                    <i v-for="icon in 3" :key="icon"></i>
                  </div>
                </div>
                <div
                  id="card"
                  :class="cardClass"
                  v-if="day.events.length!==0 && showMoreCard === day.date && (day.events.length > maxCount-1)"
                  @click.stop
                >
                  <slot name="body-card"></slot>
                </div>
              </div>
            </div>
          </template>
        </div>
      </div>
      <div class="time" ref="time" v-loading="loading" v-else-if="tableType=='week'">
        <div
          class="row"
          :class="timeDivide.length>13?'much-row':''"
          v-for="(time,index) in timeDivide"
          :key="index"
        >
          <div class="left-info" v-if="tableType=='week'">
            <div class="time-info">
              <strong>{{index+1}}</strong>
              <div class="lesson-time">
                <span>{{time.start}}</span>
                <span>~</span>
                <span>{{time.end}}</span>
              </div>
            </div>
          </div>

          <template v-if="weekDate.length">
            <div
              class="events-day"
              v-for="(item,w) in weekDate"
              :key="w"
              :class="{today: item.isToday}"
            >
              <div class="event-box" v-if="item.events.length">
                <template v-for="(event,i) in item.events">
                  <!-- <div
                    class="event-item"
                    :key="i"
                    :class="{selected: showCard == event.id}"
                    :style="`background-color: ${showCard == event.id ? '#C7E6FD' : '#409EFF'}`"
                    v-if="isTheday(item.date, event.start) && isInTime(index, event.lesson)"
                    @click="eventClick(event,$event)"
                  >-->
                  <div
                    class="time-event"
                    :key="i"
                    :class="{selected: showCard == event.id}"
                    v-if="isTheday(item.date, event.start) && isInTime(index, event.lesson)"
                    @click="eventClick(event, $event)"
                  >
                    <div class="inner">
                      <p class="name">{{event.course}}</p>
                      <p class="info">
                        <span>教室：{{event.clsRoom}}</span>
                        <span>班级：{{event.class}}</span>
                      </p>
                    </div>
                  </div>
                </template>
              </div>
            </div>
          </template>
        </div>
      </div>
      <!--  -->
      <div class="time" ref="day" v-loading="loading" v-else-if="tableType=='day'">
        <div
          class="row"
          :class="timeDivide.length>13?'much-row':''"
          v-for="(time,index) in timeDivide"
          :key="index"
        >
          <div class="left-info">
            <div class="time-info">
              <strong>{{index+1}}</strong>
              <div class="lesson-time">
                <span>{{time.start}}</span>
                <span>~</span>
                <span>{{time.end}}</span>
              </div>
            </div>
          </div>

          <template v-if="dayDate">
            <div class="events-day">
              <div class="event-box" v-if="dayDate.events.length">
                <template v-for="(event,i) in dayDate.events">
                  <div
                    class="time-event"
                    :key="i"
                    :class="{selected: showCard == event.id}"
                    v-if="isTheday(dayDate.date, event.start) && isInTime(index, event.lesson)"
                    @click="eventClick(event, $event)"
                  >
                    <div class="inner">
                      <p class="name">{{event.course}}</p>
                      <p class="info">
                        <span>教室：{{event.clsRoom}}</span>
                        <span>班级：{{event.class}}</span>
                      </p>
                    </div>
                  </div>
                </template>
              </div>
            </div>
          </template>
        </div>
      </div>
    </div>
  </div>
</template>
<script type="text/babel">
import dateFunc from "./dateFunc.js";

export default {
  props: {
    currentDate: {},
    events: {
      type: Array
    },
    timeDivide: {
      type: Array,
      required: true
    },
    weekNames: {
      type: Array,
      default: () => []
    },
    monthNames: {},
    dayName: {},
    firstDay: {},
    tableType: {},
    weekDays: {
      type: Array,
      default() {
        return dateFunc.getDates(new Date());
      }
    },
    isLimit: {
      type: Boolean,
      default: false
    },
    loading: {
      type: Boolean,
      default: true
    },
    dialogVisible: {
      type: Boolean,
      default: false
    }
  },
  created() {
    const _this = this;
    document.addEventListener("click", function(e) {
      _this.showMoreCard = "";
    });
  },
  mounted() {
    this.isNeedShowMore();
  },
  beforeDestroy() {
    const _this = this;
    document.removeEventListener("click", function(e) {
      _this.showMoreCard = "";
    });
  },
  data() {
    return {
      // weekNames : DAY_NAMES,
      weekMask: [1, 2, 3, 4, 5, 6, 7],
      // events : [],
      eventLimit: 18,
      showMore: false,
      morePos: {
        top: 0,
        left: 0
      },
      selectDay: {},
      showCard: -1,
      showMoreCard: "",
      maxCount: 5,
      cardLeft: 0,
      cardRight: 0
    };
  },

  watch: {
    currentDate() {
      this.getCalendar();
    }
  },
  computed: {
    currentDates() {
      return this.getCalendar();
    },
    weekDate() {
      return this.getWeekDate();
    },
    dayDate() {
      return this.getDayDate();
    },
    itemVisible: {
      get() {
        return this.dialogVisible;
      },
      set(val) {
        this.$emit("update:dialogVisible", val);
      }
    }
  },
  methods: {
    isNeedShowMore() {
      const winW = parseInt(window.innerWidth);
      const eventsDay = document.querySelector(".dates .events-day");
      const dayNum = document.querySelector(".dates .events-day .day-number");
      const eventItem = document.querySelector(".dates .event-item");

      if (!eventItem) return;

      const dayH = parseInt(window.getComputedStyle(eventsDay).height);
      let numH = parseInt(window.getComputedStyle(dayNum).height);
      const itemH = parseInt(window.getComputedStyle(eventItem).height);
      const itemMb = parseInt(window.getComputedStyle(eventItem).marginBottom);

      if (winW > 1400) {
        numH = numH + 6;
      } else {
        numH = numH + 6;
      }

      const wrapH = dayH - numH;
      const count = Math.floor((wrapH + itemMb) / (itemH + itemMb));
      if (wrapH - (itemH * count + itemMb * (count - 1)) > 5) {
        this.maxCount = count;
      } else {
        this.maxCount = count - 1;
      }
    },
    isBegin(event, date, index) {
      const st = new Date(event.start);

      if (index === 0 || st.toDateString() === date.toDateString()) {
        return `<span>第${event.lesson}课节</span><span>${event.course}</span>`;
      }
      return "　";
    },
    moreTitle(date) {
      const dt = new Date(date);
      return (
        this.weekNames[dt.getDay()] +
        ", " +
        this.monthNames[dt.getMonth()] +
        dt.getDate()
      );
    },
    classNames(cssClass) {
      if (!cssClass) return "";
      // string
      if (typeof cssClass === "string") return cssClass;

      // Array
      if (Array.isArray(cssClass)) return cssClass.join(" ");

      // else
      return "";
    },
    getCalendar() {
      // calculate 2d-array of each month
      // first day of this month
      const now = new Date(); // today
      const current = new Date(this.currentDate);

      const startDate = dateFunc.getStartDate(current); // 1st day of this month

      const curWeekDay = startDate.getDay();

      // begin date of this table may be some day of last month
      let diff = parseInt(this.firstDay) - curWeekDay + 1;
      diff = diff > 0 ? diff - 7 : diff;

      startDate.setDate(startDate.getDate() + diff);
      const calendar = [];
      for (let perWeek = 0; perWeek < 6; perWeek++) {
        const week = [];

        for (let perDay = 0; perDay < 7; perDay++) {
          week.push({
            monthDay: startDate.getDate(),
            isToday: now.toDateString() === startDate.toDateString(),
            isCurMonth: startDate.getMonth() === current.getMonth(),
            weekDay: perDay,
            date: new Date(startDate),
            events: this.slotEvents(new Date(startDate))
          });
          startDate.setDate(startDate.getDate() + 1);
        }
        calendar.push(week);
      }

      return calendar;
    },
    slotEvents(date) {
      const thisDayEvents = [];
      this.events.filter(event => {
        const day = new Date(event.start);
        if (date.toLocaleDateString() === day.toLocaleDateString()) {
          thisDayEvents.push(event);
        }
      });
      this.judgeTime(thisDayEvents);
      return thisDayEvents;
    },
    // 获取周视图的天元素格式化
    getWeekDate() {
      const oldWeekDays = this.weekDays;
      const newWeekDays = [];
      oldWeekDays.forEach((e, index) => {
        let isToday = false;
        if (new Date().toDateString() === e.toDateString()) {
          isToday = true;
        }
        newWeekDays.push({
          showDate: dateFunc.format(e, "MM-dd"),
          date: dateFunc.format(e, "yyyy-MM-dd"),
          isToday,
          events: this.slotTimeEvents(e) // 整理事件集合 （拿事件去比较时间，分发事件到时间插槽内）
        });
      });
      return newWeekDays;
    },
    // 获取周视图的天元素格式化
    getDayDate() {
      const theDay = {};
      theDay.date = dateFunc.format(this.currentDate, "yyyy-MM-dd");
      theDay.showDate = dateFunc.format(this.currentDate, "MM-dd");
      theDay.events = this.slotTimeEvents(this.currentDate); // 整理事件集合 （拿事件去比较时间，分发事件到时间插槽内）

      return theDay;
    },
    // 发现该时间段所有的事件
    slotTimeEvents(date) {
      let thisDayEvents = JSON.parse(JSON.stringify(this.events));
      thisDayEvents = thisDayEvents.filter(event => {
        const day = new Date(event.start);
        return date.toLocaleDateString() === day.toLocaleDateString();
      });

      this.judgeTime(thisDayEvents);
      return thisDayEvents;
    },
    judgeTime(arr) {
      arr.forEach(event => {
        const dayS = new Date(event.start);
        // 加上时间戳后判断时间段
        const hourS = dayS.getHours();
        const minuteS = dayS.getMinutes();
        let week = dayS.getDay();
        if (week === 0) {
          week = 7;
        }

        let lesson;

        this.timeDivide.forEach((td, i) => {
          const [hs, ms] = td.start.split(":");
          if (
            parseInt(hs) === parseInt(hourS) &&
            parseInt(ms) === parseInt(minuteS)
          ) {
            lesson = i + 1;
          }
        });

        event.lesson = lesson;
        event.weekDay = this.weekNames[Number(week) - 1];
        event.weekDayIndex = week;
      });
    },
    isTheday(day1, day2) {
      return new Date(day1).toDateString() === new Date(day2).toDateString();
    },
    isStart(eventDate, date) {
      const st = new Date(eventDate);
      return st.toDateString() === date.toDateString();
    },
    isEnd(eventDate, date) {
      const ed = new Date(eventDate);
      return ed.toDateString() === date.toDateString();
    },
    isInTime(index, lesson) {
      return lesson === index + 1;
    },
    selectThisDay(day, jsEvent) {
      this.selectDay = day;
      this.showMore = true;
      this.morePos = this.computePos(event.target);
      this.morePos.top -= 100;
      const events = day.events.filter(item => {
        return item.isShow === true;
      });
      this.$emit("moreclick", day.date, events, jsEvent);
    },
    computePos(target) {
      const eventRect = target.getBoundingClientRect();
      const pageRect = this.$refs.dates.getBoundingClientRect();
      return {
        left: eventRect.left - pageRect.left,
        top: eventRect.top + eventRect.height - pageRect.top
      };
    },
    eventClick(event, jsEvent) {
      this.showCard = event.id;
      jsEvent.stopPropagation();
      this.itemVisible = true;
      this.$emit("eventclick", event);
    },
    dayClick(day, jsEvent) {
      // console.log(event,jsEvent, 'evenvet')
      this.showMoreCard = day.date;
      jsEvent.stopPropagation();
      // let pos = this.computePos(jsEvent.target)
      this.$emit("dayclick", day, jsEvent);
      const x = jsEvent.x;
      const y = jsEvent.y;

      // 判断出左右中三边界的取值范围进而分配class
      if (x > 400 && x < window.innerWidth - 300) {
        this.cardClass = "center-card";
      } else if (x <= 400) {
        this.cardClass = "left-card";
      } else {
        this.cardClass = "right-card";
      }
      if (y > window.innerHeight - 300) {
        this.cardClass += " " + "bottom-card";
      }
    }
  }
};
</script>
