<template>
  <div class="week_picker" v-loading="Loading">
    <el-card class="range" shadow="hover">

      <div :style="{width: '100%'}">
        <div class="range-pick">
          <div>{{dtStart}} - {{dtEnd}}</div>
          <el-date-picker :picker-options="pickerOptions" v-model="dateRange" type="daterange" size="mini"
                          :editable="false" :clearable="false" start-placeholder="开始日期" end-placeholder="结束日期"
                          @change="ChangeWeekRange"></el-date-picker>
        </div>
      </div>
    </el-card>
    <div class="weeks">
      <el-card shadow="hover">
        <div @click="PreWeek">
          <i class="el-icon-arrow-left" :style="{fontSize:'20px', lineHeight:'40px'}"/>
        </div>
      </el-card>
      <el-card :shadow="day.date.isSame(weekDate, 'day') ? 'always' : 'hover'" v-for="day in days" :key="day.text"
               @click.native="ChangeDay(day)">
        <div class="week">{{day.text}}</div>
        <div class="day" v-if="!Concise">{{day.day}}</div>
      </el-card>
      <el-card shadow="hover">
        <div @click="NextWeek">
          <i class="el-icon-arrow-right" :style="{fontSize:'20px', lineHeight:'40px'}"/>
        </div>
      </el-card>
    </div>
  </div>
</template>

<script>
import moment from "moment";

moment.locale("zh-cn");
export default {
  props: {
    // 加载中
    Loading: {
      type: Boolean,
      default: false
    },
    // 当前选中的日期
    value: {
      type: Array,
      default: () => {
        return [];
      }
    },
    // 简洁模式
    Concise: {
      type: Boolean,
      default: false
    }
  },
  data () {
    return {
      dtStart: null,
      dtEnd: null,
      pickerOptions: {
        firstDayOfWeek: 1,
        shortcuts: [
          {
            text: "最近一周",
            onClick (picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit("pick", [start, end]);
            }
          },
          {
            text: "最近一个月",
            onClick (picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
              picker.$emit("pick", [start, end]);
            }
          },
          {
            text: "最近三个月",
            onClick (picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
              picker.$emit("pick", [start, end]);
            }
          }
        ]
      },
      dateRange: [],
      weekDate: this.value,
      days: []
    };
  },
  mounted () {
    this.weekDate = moment();
    this.CalOrgin();
  },
  methods: {
    /**
     * 计算起始
     */
    CalOrgin () {
      // const weekOfday = moment(this.weekDate).format("E"); // 计算今天是这周第几天
      // this.dtStart = moment(this.weekDate).subtract(weekOfday - 1, "days").format("YYYY年MM月DD日"); // 周一日期
      // this.dtEnd = moment(this.weekDate).add(7 - weekOfday, "days").format("YYYY年MM月DD日"); // 周日日期
      // this.days = [];
      // for (let i = 0; i < 7; i++) {
      //   const date = moment(this.dtStart, "YYYY年MM月DD日").add(i, "days");
      //   this.days.push({
      //     date: date,
      //     text: this.Concise ? date.format("dd") : date.format("dddd"),
      //     day: date.format("YYYY年MM月DD日"),
      //     shadow: date.isSame(this.weekDate, "day") ? "always" : "hover",
      //     count: 0
      //   });
      // }
      //
      // this.dateRange = [
      //   moment(this.dtStart, "YYYY年MM月DD日").format("YYYY-MM-DD"),
      //   moment(this.dtEnd, "YYYY年MM月DD日").format("YYYY-MM-DD")
      // ];
      this.monthPickerDeal();
      this.weekPickerDeal();

      this.$emit("update:value", this.dateRange);
      this.$emit("ValueChanged", this.dateRange);
      // this.$emit("update:value", this.weekDate.format("YYYY-MM-DD"));
      // this.$emit("ValueChanged", this.weekDate.format("YYYY-MM-DD"),
      //   [
      //     moment(this.dtStart, "YYYY年MM月DD日").format("YYYY-MM-DD"),
      //     moment(this.dtEnd, "YYYY年MM月DD日").format("YYYY-MM-DD")
      //   ],
      //   dayCouts => this.BuildCount(dayCouts)
      // );
    },
    weekPickerDeal () {
      if (this.weekDate) {
        const weekOfday = moment(this.weekDate).format("E"); // 计算今天是这周第几天
        this.dtStart = moment(this.weekDate).subtract(weekOfday - 1, "days").format("YYYY年MM月DD日"); // 周一日期
        this.dtEnd = moment(this.weekDate).add(7 - weekOfday, "days").format("YYYY年MM月DD日"); // 周日日期
        this.days = [];
        for (let i = 0; i < 7; i++) {
          const date = moment(this.dtStart, "YYYY年MM月DD日").add(i, "days");
          this.days.push({
            date: date,
            text: this.Concise ? date.format("dd") : date.format("dddd"),
            day: date.format("YYYY年MM月DD日"),
            shadow: date.isSame(this.weekDate, "day") ? "always" : "hover",
            count: 0
          });
        }
      } else {
        const weekOfday = moment(this.dateRange[0]).format("E"); // 计算今天是这周第几天
        const dateStart = moment(this.dateRange[0]).subtract(weekOfday - 1, "days").format("YYYY年MM月DD日"); // 周一日期
        // this.dtEnd = moment(this.dateRange[0]).add(7 - weekOfday, "days").format("YYYY年MM月DD日"); // 周日日期
        this.days = [];
        for (let i = 0; i < 7; i++) {
          const date = moment(dateStart, "YYYY年MM月DD日").add(i, "days");
          this.days.push({
            date: date,
            text: this.Concise ? date.format("dd") : date.format("dddd"),
            day: date.format("YYYY年MM月DD日"),
            shadow: date.isSame(this.weekDate, "day") ? "always" : "hover",
            count: 0
          });
        }
      }
    },
    monthPickerDeal () {
      if (this.weekDate) {
        this.dateRange = [
          moment(this.weekDate, "YYYY年MM月DD日").format("YYYY-MM-DD"),
          moment(this.weekDate, "YYYY年MM月DD日").format("YYYY-MM-DD")
        ];
      } else {
        this.dateRange = [
          moment(this.dtStart, "YYYY年MM月DD日").format("YYYY-MM-DD"),
          moment(this.dtEnd, "YYYY年MM月DD日").format("YYYY-MM-DD")
        ];
      }
    },
    /**
     * 通过日历控件切换
     */
    ChangeWeekRange (value) {
      this.weekDate = null;
      this.dtStart = moment(this.dateRange[0]).format("YYYY年MM月DD日");
      this.dtEnd = moment(this.dateRange[1]).format("YYYY年MM月DD日");
      console.log("ChangeWeekRange:", value);
      this.CalOrgin();
    },
    /**
     * 上一周
     */
    PreWeek () {
      // this.weekDate = moment(this.weekDate).add(-1, "weeks");
      this.WeekAdd(-1);
    },
    /**
     * 下一周
     */
    NextWeek () {
      this.WeekAdd(1);
    },
    WeekAdd (val) {
      const start = moment(this.dtStart, "YYYY年MM月DD日").format("YYYY-MM-DD");
      const weekOfday = moment(start).format("E"); // 计算今天是这周第几天
      const dateStart = moment(start).subtract(weekOfday - 1, "days"); // 周一日期

      // start = moment(start).format("E");
      // const end = moment(this.dtEnd, "YYYY年MM月DD日").format("YYYY-MM-DD");
      this.weekDate = null;
      const startDate = moment(dateStart).add(val, "weeks");
      const endDate = moment(dateStart).add(val, "weeks").add(6, "days");
      this.dateRange = [
        startDate.format("YYYY-MM-DD"),
        endDate.format("YYYY-MM-DD")
      ];
      this.dtStart = startDate.format("YYYY年MM月DD日");
      this.dtEnd = endDate.format("YYYY年MM月DD日");
      this.CalOrgin();
    },
    /**
     * 点击切换
     */
    ChangeDay (dayInfo) {
      this.weekDate = dayInfo.date;
      this.CalOrgin();
    }
  }
};
</script>

<style lang="less">
.week_picker {
  margin-top: @Margin;

  .range {
    @height: 35px;

    .range-pick {
      width: 100%;
      text-align: center;
    }

    .el-card__body {
      height: @height;
      padding: 0px @Margin / 2;
      width: calc(100% - @Margin);
      display: flex;
      justify-content: space-between;
      align-items: center;

      > i {
        width: @Margin;
        height: @Margin;
        line-height: @Margin;
        text-align: center;
        border-radius: @Margin;
        border: 1px solid rgba(0, 0, 0, 0.5);
        cursor: pointer;
      }

      > div {
        overflow: hidden;
        height: 100%;
        line-height: @height;

        .el-date-editor {
          height: 100%;
          display: block;
          margin-top: 0 - @height;
          opacity: 0;
          transform: translateX(-50%);
          left: 50%;

          .el-input__inner {
            border: none;
          }
        }
      }
    }
  }

  .weeks {
    margin-top: @Margin / 2;
    display: flex;
    justify-content: space-between;
    border: 1px solid #ebeef5;

    .el-card {
      width: 100%;
      display: flex;
      text-align: center;
      border: none;

      .el-card__body {
        width: 100%;
        padding: 10px 15px;

        .el-badge {
          width: 100%;

          .day {
            color: #666666;
          }

          .week {
            color: #333333;
          }

          .el-badge__content {
            width: 30px;
            right: 35px;
            top: 5px;
          }
        }
      }
    }
  }
  @media (min-width: 1000px) and (max-width: 1600px) {
    .weeks .el-card .el-card__body {
      padding: 8px;
    }
  }
}
</style>
