<template>
  <el-dialog
    id="sform"
    :visible.sync="Visible"
    :modal-append-to-body="false"
    :close-on-click-modal="false"
    :destroy-on-close="true"
    :before-close="OnDlgClose"
    center
  >
    <template slot="title">
      {{ title }}
      <el-tooltip placement="left" content="同步该录制时间的课表信息">
        <el-button
          icon="el-icon-refresh"
          size="mini"
          @click="SyncSecdule"
          v-if="data"
        />
      </el-tooltip>
    </template>
    <el-form
      size="mini"
      label-width="80px"
      :model="ResourceForm"
      ref="ResourceForm"
      :rules="rules"
    >
      <el-row :gutter="10">
        <el-col :span="8">
          <el-form-item label="课程类目" prop="category">
            <el-select v-model="ResourceForm.category" placeholder="请选择">
              <el-option
                v-for="item in catData"
                :key="item.value"
                :label="item.label"
                :value="item.value"
              ></el-option>
            </el-select>
          </el-form-item>
        </el-col>
        <el-col :span="8">
          <el-form-item label="课程名称" prop="CourseName">
            <el-input v-model="ResourceForm.CourseName"></el-input>
          </el-form-item>
        </el-col>
        <el-col :span="8">
          <el-form-item label="授课老师" prop="Teachers">
            <el-input v-model="ResourceForm.Teachers"></el-input>
          </el-form-item>
        </el-col>
        <el-col :span="8">
          <el-form-item label="录制时间" prop="RecTime">
            <el-date-picker
              type="datetime"
              v-model="ResourceForm.RecTime"
              placeholder="请选择"
              value-format="yyyy-MM-dd HH:mm:ss"
              :clearable="false"
            ></el-date-picker>
          </el-form-item>
        </el-col>
        <el-col :span="8">
          <el-form-item label="所属专业" prop="Discipline">
            <el-input v-model="ResourceForm.Discipline"></el-input>
          </el-form-item>
        </el-col>
        <el-col :span="8">
          <el-form-item label="上课班级" prop="Classes">
            <el-input v-model="ResourceForm.Classes"></el-input>
          </el-form-item>
        </el-col>
      </el-row>
      <el-row :gutter="10">
        <el-col>
          <el-form-item label="备注" prop="Note">
            <el-input type="textarea" v-model="ResourceForm.Note"></el-input>
          </el-form-item>
        </el-col>
      </el-row>
    </el-form>
    <PlayBox
      ref="PlayBox"
      :Videos.sync="videoList"
      ShowThumb
      :checked.sync="SelectedItems"
    />
    <div class="attch">
      <span>附件列表</span>
      <el-upload action :limit="1" :before-upload="ChangeUp" class="upload">
        <el-button
          onfocus="this.blur()"
          type="primary"
          icon="el-icon-upload"
          size="mini"
          plain
          >添加</el-button
        >
      </el-upload>
      <el-table :data="attchData" row-key="id">
        <el-table-column
          prop="fileName"
          label="附件名称"
          align="center"
        ></el-table-column>
        <el-table-column
          prop="extension"
          label="附件类型"
          align="center"
        ></el-table-column>
        <el-table-column
          prop="fileSize"
          label="文件大小"
          align="center"
        ></el-table-column>
        <el-table-column label="操作" align="center">
          <template slot-scope="scope">
            <el-button
              size="mini"
              type="danger"
              plain
              @click="AttDelete(scope.$index)"
              >删除</el-button
            >
          </template>
        </el-table-column>
      </el-table>
    </div>
    <div slot="footer">
      <el-checkbox-group v-model="ResourceForm.checkList">
        <el-checkbox label="允许评论" />
        <!-- <el-checkbox label="允许下载" /> -->
      </el-checkbox-group>
      <el-button-group>
        <el-button
          type="primary"
          icon="el-icon-check"
          size="mini"
          autofocus
          native-type="submit"
          @click="Confirm"
          >确 定</el-button
        >
        <el-button icon="el-icon-close" size="mini" @click="OnDlgClose"
          >取 消</el-button
        >
      </el-button-group>
    </div>
  </el-dialog>
</template>
<script>
import api from "@/scripts/api";
import moment from "moment";
import store from "@/scripts/store";
let ifUpload = true;
export default {
  components: {
    PlayBox: () => import("@/components/Player/PlayBox")
  },
  props: {
    Visible: {
      type: Boolean,
      default: false
    },
    // 标题
    title: {
      type: String,
      default: ""
    },
    // 编号
    id: {
      type: String,
      default: ""
    },
    data: {
      type: Object,
      default: () => null
    }
  },
  data() {
    return {
      SelectedItems: [],
      videoList: [],
      attchData: [],
      ResourceForm: {
        CourseName: "",
        Teachers: "",
        Classes: "",
        Note: "",
        Discipline: "",
        RecTime: "",
        category: "",
        checkList: [],
        mId: [],
        AttchId: ""
      },
      rules: {
        category: [
          {
            required: true,
            message: "请选择发布的类目",
            trigger: "blur"
          }
        ],
        CourseName: [
          {
            required: true,
            message: "请填写资源标题",
            trigger: "blur"
          }
        ],
        Teachers: [
          {
            required: true,
            message: "请填写授课老师",
            trigger: "blur"
          }
        ],
        RecTime: [
          {
            required: true,
            message: "请填写录制时间",
            trigger: "change"
          }
        ],
        Discipline: [
          {
            required: true,
            message: "请填写所属专业",
            trigger: "blur"
          }
        ]
      },
      catData: []
    };
  },
  watch: {
    async data(newVal, oldVal) {
      if (newVal) {
        this.ResourceForm.RecTime = newVal.MaterialCreateDate;
        this.ResourceForm.Note = newVal.ScheduleInfo.Note;
        try {
          this.ResourceForm.Teachers =
            newVal.ScheduleInfo.Teachers !== ""
              ? JSON.parse(newVal.ScheduleInfo.Teachers).join("、")
              : "";
        } catch {
          this.ResourceForm.Teachers = newVal.ScheduleInfo.Teachers;
        }
        this.ResourceForm.CourseName = newVal.ScheduleInfo.CourseName;
        try {
          this.ResourceForm.Classes =
            newVal.ScheduleInfo.Classes !== ""
              ? JSON.parse(newVal.ScheduleInfo.Classes).join("、")
              : "";
        } catch {
          this.ResourceForm.Classes = newVal.ScheduleInfo.Classes;
        }
        const videoList = [];
        if (newVal.MaterialInfo) {
          await newVal.MaterialInfo.forEach(ele => {
            if (ele.Type === "MP4") {
              videoList.push(ele);
            }
          });
        }

        this.videoList = videoList;
      }
    },
    async id(newVal, oldVal) {
      if (newVal) {
        this.ResourceForm.checkList = [];
        const res = await api.ElaborateCourses.GetData(newVal);
        if (res) {
          this.$set(res.Data, "ECInfo", JSON.parse(res.Data.ECInfo));
          this.ResourceForm.RecTime = res.Data.ECInfo.RecTime;
          this.ResourceForm.Note = res.Data.ECInfo.Note;
          this.ResourceForm.Teachers = res.Data.ECInfo.Teachers;
          this.ResourceForm.CourseName = res.Data.ECInfo.CourseName;
          this.ResourceForm.Classes = res.Data.ECInfo.Classes;
          this.ResourceForm.Discipline = res.Data.ECInfo.Discipline;
          this.ResourceForm.category = res.Data.ECType;
          if (res.Data.ECAllowComment === 1) {
            this.ResourceForm.checkList.push("允许评论");
          }
          if (res.Data.ECAllowDownload === 1) {
            this.ResourceForm.checkList.push("允许下载");
          }

          const videoList = [];
          res.Materials.forEach(element => {
            this.$set(element, "Path", element.MaterialStorePath);
            videoList.push(element);
          });

          this.videoList = videoList;

          this.attchData = res.Attachs;
          this.attchData.forEach(element => {
            this.$set(element, "fileName", element.MaterialName);
            this.$set(element, "fileSize", element.MaterialFileSize);
            this.$set(element, "extension", element.MaterialFileExt);
          });
        }
      }
    }
  },
  beforeDestroy() {
    this.Dispose();
  },
  mounted() {
    this.GetCatory();
  },
  methods: {
    OnDlgClose() {
      this.attchData = [];
      this.$emit("update:visible", false);
      this.$emit("Close");
      this.$refs.ResourceForm.resetFields();
      this.Dispose();
    },
    async SyncSecdule() {
      const res = await api.Schedule.GetSheduleByTime(
        this.data.ClsRoomID,
        this.ResourceForm.RecTime,
        this.ResourceForm.RecTime
      );

      if (res && res.Datas) {
        const thcs = res.Datas.ThcINFO ? JSON.parse(res.Datas.ThcINFO) : [];
        let thcNames = "";
        if (thcs) {
          thcs.forEach((t, i) => {
            thcNames += t.TeacherName;
            if (i < thcs.length - 1) thcNames += "，";
          });
        }

        let Classes = "";
        const clses = res.Datas.ClassInfo
          ? JSON.parse(res.Datas.ClassInfo)
          : [];
        if (clses) {
          clses.forEach((c, i) => {
            Classes += c.ClassName;
            if (i < clses.length - 1) Classes += "，";
          });
        }

        this.ResourceForm.RecTime =
          moment(this.ResourceForm.RecTime).format("YYYY-MM-DD") +
          " " +
          res.Datas.LessonClsTime;
        this.ResourceForm.Teachers = thcNames;
        this.ResourceForm.Classes = Classes;
        this.ResourceForm.CourseName = res.Datas.CourseName;
      }
    },
    // 附件列表删除
    AttDelete(index) {
      this.attchData.splice(index, 1);
    },
    // 文件变化
    ChangeUp(file) {
      if (file.size > 1024 * 1024 * 500) {
        this.$message({
          showClose: true,
          message: "文件大小不能超过500MB",
          type: "error"
        });
        ifUpload = false;
        return false;
      }
      const arr = ["cmd", "exe", "bat", "vbs"];
      const index = file.name.lastIndexOf(".");
      const suff = file.name.slice(index + 1);
      if (arr.indexOf(suff) > -1) {
        this.$message({
          showClose: true,
          message: "不支持的文件类型",
          type: "error"
        });
        ifUpload = false;
        return false;
      }
      if (ifUpload) {
        this.AddAttach(file);
        return false;
      }
    },
    // 添加上传
    async AddAttach(file) {
      const fd = new FormData();
      const userInfo = store.GetUserInfo();
      fd.append("stream", file);
      fd.append("dirid", "");
      fd.append("username", userInfo.UserName);
      fd.append("Token", "");
      const res = await api.Material.AddAttachments(fd);
      if (!res.Result) {
        this.$message({
          showClose: true,
          message: "添加失败",
          type: "error"
        });
      } else {
        this.attchData.push(res);
      }
    },
    // 释放
    Dispose() {
      this.videoList = [];
      if (this.$refs.PlayBox) this.$refs.PlayBox.Dispose();
    },
    // 获取资源条目
    async GetCatory() {
      this.catData = [];
      const res = await api.Metadata.GetTrees("JPKCLM");
      if (res.Result) {
        res.Datas.forEach(element => {
          if (element.MetadataCode !== "JPKCLM") {
            this.catData.push({
              value: element.MetadataCode,
              label: element.MetadataName
            });
          }
        });
      }
    },
    // 发布表单保存
    Confirm() {
      this.$refs.ResourceForm.validate(async valid => {
        if (valid) {
          let ECInfo = {
            Teachers: this.ResourceForm.Teachers,
            RecTime: this.ResourceForm.RecTime,
            Classes: this.ResourceForm.Classes,
            CourseName: this.ResourceForm.CourseName,
            Discipline: this.ResourceForm.Discipline,
            Note: this.ResourceForm.Note
          };
          ECInfo = JSON.stringify(ECInfo).toString();
          const ec = this.ResourceForm.checkList[0] ? 1 : 0;
          const ed = this.ResourceForm.checkList[1] ? 1 : 0;
          const userInfo = store.GetUserInfo();

          if (this.id) {
            const param = {
              ecid: this.id,
              RTName: this.ResourceForm.CourseName,
              category: this.ResourceForm.category,
              ECInfo: ECInfo,
              ecallowComment: ec,
              ecallowDownload: ed,
              pid: userInfo.UserID,
              midlist: JSON.stringify(this.SelectedItems),
              AttchId: JSON.stringify(this.attchData)
            };

            const pdata = Object.values(param);
            const res = await api.ElaborateCourses.Update(...pdata);
            this.showSource = false;
            if (res.Result) {
              this.OnDlgClose();
            } else {
              this.$message({
                showClose: true,
                message: "编辑失败",
                type: "error"
              });
            }
          } else {
            const param = {
              RTName: this.ResourceForm.CourseName,
              category: this.ResourceForm.category,
              ECInfo: ECInfo,
              ecallowComment: ec,
              ecallowDownload: ed,
              pid: userInfo.UserID,
              pname: userInfo.UserName,
              aid: userInfo.UserID,
              aname: userInfo.UserName,
              midlist: JSON.stringify(this.SelectedItems),
              AttchId: JSON.stringify(this.attchData)
            };
            const pdata = Object.values(param);
            const res = await api.Material.ReleaseRes(...pdata);
            this.showSource = false;
            if (res.Result) {
              this.OnDlgClose();
            } else {
              this.$message({
                showClose: true,
                message: "发布失败",
                type: "error"
              });
            }
          }
        } else {
          return false;
        }
      });
    }
  }
};
</script>
<style lang="less">
#sform {
  .attch {
    position: relative;
    // margin-top: -18px;
    > span {
      line-height: 50px;
    }
    .upload {
      position: absolute;
      right: 0px;
      top: 10px;
    }
    .el-upload-list {
      display: none;
    }
  }

  .el-dialog {
    width: 1001px;
    height: 90%;

    .el-dialog__header {
      font-size: 17px;

      .el-button {
        background: none;
        position: absolute;
        border: none;
        right: 40px;
        top: 15px;
        font-size: 16px;
      }
    }

    .el-dialog__body {
      padding: 10px 20px;
      overflow: hidden;
      overflow-y: auto;

      .el-form {
        .el-row:last-child {
          .el-form-item {
            // margin-bottom: 0px;
          }
        }
      }

      .vb {
        height: 400px;
        border: 1px solid #dddddd;
      }
    }

    .el-dialog__footer {
      > div {
        position: relative;
        .el-checkbox-group {
          position: absolute;
          left: 0px;
        }
      }
    }
  }
}
</style>
